html {
  @include no-cush;
  font-size: 16px; }

body {
  background: #444;
  color: #fff;
  font-family: $font-2; }

.grid {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  grid-gap: 1em;
  @media (min-width: 950px) {
    grid-template-columns: 7fr 5fr;
    grid-template-rows: auto; } }
